html {
  scroll-behavior: smooth;
}

.btn-toggle {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2rem;
  height: 2rem;
  border-radius: 1rem !important;
  transition: all 1s !important;
}

.btn-toggle span {
  display: none;
}

.btn-toggle:hover > span {
  display: inline-block;
  font-size: 1rem;
  white-space: nowrap;
}

.btn-toggle:hover {
  width: 125px;
  border-radius: 0.25rem !important;
}

.btn-facebook {
  color: var(--white) !important;
  background: linear-gradient(
    222deg,
    #8b9dc3 10%,
    #8b9dc3 30%,
    #3b5999 100%
  ) !important;
  background: -webkit-linear-gradient(
    222deg,
    #8b9dc3 10%,
    #8b9dc3 30%,
    #3b5999 100%
  ) !important;
  background-color: #3b5999;
  border-color: #9eb5e9 !important;
}
.btn-facebook:hover:not(.btn-toggle) {
  background: #3b5999 !important;
}

.btn-instagram {
  color: var(--white) !important;
  background: linear-gradient(
    60deg,
    #f4931b 10%,
    #d83452 30%,
    #533f9d 100%
  ) !important;
  background: -webkit-linear-gradient(
    60deg,
    #f4931b 10%,
    #d83452 30%,
    #533f9d 100%
  ) !important;
  background-color: #d83452;
  border-color: #ebb3be !important;
}
.btn-instagram:hover:not(.btn-toggle) {
  background: #d83452 !important;
}

.btn-whatsapp {
  color: var(--white) !important;
  background: linear-gradient(
    87deg,
    #075e54 10%,
    #128c7e 30%,
    #25d366 100%
  ) !important;
  background-color: #25d366 !important;
  border-color: #abebb7 !important;
}
.btn-whatsapp:hover:not(.btn-toggle) {
  background: #0cb12a !important;
}

.card {
  margin-bottom: 30px;
  box-shadow: 0 0 2rem 0 rgba(136, 152, 170, 0.15);
  border: 0;
}

.close {
  transition: all 0.15s ease;
}
.close > span:not(.sr-only) {
  background-color: transparent;
  color: rgba(0, 0, 0, 0.6);
  line-height: 17px;
  height: 1.25rem;
  width: 1.25rem;
  border-radius: 50%;
  font-size: 1.25rem;
  display: block;
  transition: all 0.15s ease;
}
.close:hover,
.close:focus {
  background-color: transparent;
  color: rgba(0, 0, 0, 0.9);
  outline: none;
}
.close:hover span:not(.sr-only),
.close:focus span:not(.sr-only) {
  background-color: transparent;
}

.close-dark > span:not(.sr-only) {
  color: rgba(255, 255, 255, 0.8);
}

.close-dark:hover > span:not(.sr-only),
.close-dark:focus > span:not(.sr-only) {
  color: var(--white);
}

textarea[resize="none"] {
  resize: none !important;
}

textarea[resize="both"] {
  resize: both !important;
}

textarea[resize="vertical"] {
  resize: vertical !important;
}

textarea[resize="horizontal"] {
  resize: horizontal !important;
}

p {
  font-size: 1rem;
  font-weight: 300;
  line-height: 1.7;
}

article h4:not(:first-child),
article h5:not(:first-child) {
  margin-top: 3rem;
}

article h4,
article h5 {
  margin-bottom: 1.5rem;
}

article figure {
  margin: 3rem 0;
}

article h5 + figure {
  margin-top: 0;
}

.display-1 span,
.display-2 span,
.display-3 span,
.display-4 span {
